import React, { useState, useRef, useEffect } from 'react'
import {  } from '../../../../api/list'
import List from '../../../../components/list'
import './css/index.css'
import { MinusCircleOutlined } from '@ant-design/icons/lib/icons'

const MeetingManagement = () => {
    const [action] = useState<any>({
        title: '操作',
        key: 'action',
        width: 60,
        fixed: 'right',
        render: (text: any) => (
            <div>
                <MinusCircleOutlined style={{color: '#E3604F', cursor: 'pointer', fontSize: 18}}></MinusCircleOutlined>
            </div>
        ),
    });

    const [statusScreen] = useState<Array<any>>([
        { name: '正在进行', status: 0 },
        { name: '未开始', status: 1 },
        { name: '已结束', status: 2 },
        { name: '所有', status: 3 },
    ]);
    const active = useRef<number>(3);
    const [random, setRandom] = useState<string>('');
    const list = useRef<any>()

    //状态筛选
    const onScreen = (item: any): void => {
        active.current = item.status
        if(active.current !== 3) {
            list.current.searchArr.current.push({
                name: 'status',
                value: active.current,
                rule: 1
            })
        }
        list.current.getAllList()
        setRandom(Math.random().toString())
    }

    return (
        <div className="meeting-anagement">
            <List ref={list} action={action} listUrl="/meet/meetingList/getAll" columnsUrl="/meet/meetingList/getPageAttr">
                <div className="anagement-screen">
                    {
                        statusScreen.map((item: any, index: number) => {
                            return (
                                <p key={index}>
                                    <span onClick={() => onScreen(item)} style={{color: active.current === index ? '#40a9ff' : ''}}>{ item.name }</span>
                                    <span style={{display: index === statusScreen.length - 1 ? 'none' : ''}}>/</span>
                                </p>
                            )
                        })
                    }
                </div>
            </List>
        </div>
    )
}

export default MeetingManagement
