import React, { useEffect, useState, useRef } from 'react'
import moment from 'moment'
import './css/index.css'
import { DatePicker } from 'antd';
import { SyncOutlined } from '@ant-design/icons/lib/icons'
import { requestDayRegistrations, requestDayAmount, requestMeeting, requestEnterpriseDayRegistration } from '../../../../api/workbench'
import ReactEcharts from 'echarts-for-react';
import * as echarts from 'echarts'

const { RangePicker } = DatePicker;

const DataDaily = () => {
    const init = async () => {
        // let start: number = new Date().getTime() - 1000 * 60 * 60 * 24 * 2
        // let end: number = new Date().getTime() - 1000 * 60 * 60 * 24
        await getDayRegistrations(start.current, end.current)
        await getEnterpriseDayRegistration(start.current, end.current)
        await getDayAmount(start.current, end.current)
        await getMeeting(start.current, end.current)
        let dates: string[] = getBetweenDateStr(moment(start.current).format('YYYY-MM-DD'), moment(end.current).format('YYYY-MM-DD'))
        await getOption(dates)
    }

    const start = useRef<number>(new Date().getTime() - 1000 * 60 * 60 * 24 * 2);
    const end = useRef<number>(new Date().getTime() - 1000 * 60 * 60 * 24);
    const [registrant, setRegistrant] = useState<number>(0);
    const [enterpriseAcount, setEnterpriseAcount] = useState<number>(0);
    const [amount, setAmount] = useState<number>(0);
    const [amountCompare, setamountCompare] = useState<number>(0);
    const amountCount = useRef<any>({ front: 0, rear:  0});
    const [meeting, setMeeting] = useState<number>(0);
    const [meetingCompare, setMeetingCompare] = useState<number>(0);
    const meetingCount = useRef<any>({ front: 0, rear:  0})
    const [option, setOption] = useState<any>({});
    const optionData = useRef<any>({
        time: [],
        toRegister: [],
        enterpriseRegister: [],
        active: [],
        meeting: []
    })
    const charts1 = useRef<any>()

    useEffect(() => {
        init()
    }, [])

    //日注册量
    const getDayRegistrations = async (_start: number, _end: number): Promise<any> => {
        let response: any = await requestDayRegistrations({ startTime: _start, endTime: _end })
        let num: number = 0
        response.data.forEach((item: any) => {
            num += item.total
            optionData.current.toRegister.push(num)
        })
        setRegistrant(num)
    }

    //机构日注册量
    const getEnterpriseDayRegistration = async (_start: number, _end: number): Promise<any> => {
        let response: any = await requestEnterpriseDayRegistration({ startTime: _start, endTime: _end })
        let num: number = 0
        response.data.forEach((item: any) => {
            num += item.total
            optionData.current.enterpriseRegister.push(num)
        })
        setEnterpriseAcount(num)
    }

    //日活跃量
    const getDayAmount = async (_start: number, _end: number): Promise<any> => {
        let response: any = await requestDayAmount({ startTime: _start, endTime: _end })
        let num: number = 0
        response.data.forEach((item: any) => {
            if(item.total) {
                num += item.total
                optionData.current.active.push(num)
            } else {
                optionData.current.active.push(0)
            }
        })
        try {
            amountCount.current.front = response.data[response.data.length - 2].total
            amountCount.current.rear = response.data[response.data.length - 1].total
        } catch {
            amountCount.current.front = 0
            amountCount.current.rear = 0
        }
        
        setamountCompare(amountCount.current.rear - amountCount.current.front)
        setAmount(num)
    }

    //日会议量
    const getMeeting = async (_start: number, _end: number): Promise<any> => {
        let response: any = await requestMeeting({ startTime: _start, endTime: _end })
        let num: number = 0
        response.data.forEach((item: any) => {
            if(item.total) {
                num += item.total
                optionData.current.meeting.push(num)
            } else {
                optionData.current.meeting.push(0)
            }
        })
        try {
            meetingCount.current.front = response.data[response.data.length - 2].total
            meetingCount.current.rear = response.data[response.data.length - 1].total
        } catch {
            meetingCount.current.front = 0
            meetingCount.current.rear = 0
        }
        setMeetingCompare(meetingCount.current.rear - meetingCount.current.front)
        setMeeting(num)
    }

    const compare = (front: number, rear: number): boolean => {
        let str: boolean = true
        if(rear >=  front) {
            str = true
        } else if(rear < front) {
            str = false
        }
        return str
    }

    const getOption = (arr: any) => {
        let myChart: any = echarts.init(charts1.current);

        const xData = arr

        let newOption: any = {
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['日注册量', '日机构注册量', '日活跃量', '日会议量']
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: xData
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '日注册量',
                    type: 'line',
                    data: optionData.current.toRegister,
                    smooth: true
                },
                {
                    name: '日机构注册量',
                    type: 'line',
                    data: optionData.current.enterpriseRegister,
                    smooth: true
                },
                {
                    name: '日活跃量',
                    type: 'line',
                    data: optionData.current.active,
                    smooth: true
                },
                {
                    name: '日会议量',
                    type: 'line',
                    data: optionData.current.meeting,
                    smooth: true
                }
            ]
        }

        setOption(newOption)
        myChart.setOption(newOption)

        window.addEventListener("resize", () => myChart.resize())
    }
    
    const getBetweenDateStr = (start: string, end: string): string[] => {
        let stime: any = start
        let etime: any = end

        var diffdate = new Array();
        var i=0;
        while(stime <= etime){
            diffdate[i] = stime;
            let stime_ts = new Date(stime).getTime();
            let next_date = stime_ts + (24 * 60 * 60 * 1000);
            let next_dates_y = new Date(next_date).getFullYear() + '-';
            let next_dates_m = (new Date(next_date).getMonth() + 1 < 10) ? '0' + (new Date(next_date).getMonth() + 1) + '-':(new Date(next_date).getMonth()+1)+'-';
            let next_dates_d = (new Date(next_date).getDate() < 10) ? '0' + new Date(next_date).getDate():new Date(next_date).getDate();
            stime = next_dates_y + next_dates_m + next_dates_d;
            i++;
        }
        return diffdate
    };

    const reset = (): void => {
        start.current = new Date().getTime() - 1000 * 60 * 60 * 24 * 2
        end.current = new Date().getTime() - 1000 * 60 * 60 * 24
        init()
    }

    return (
        <>
            <div style={{position: 'relative', overflow: 'hidden', backgroundColor: '#fff', borderRadius: 4}}>
                <p className="daily-title">24小时实时数据：{ moment(start.current).format('YYYY-MM-DD') } - { moment(end.current).format('YYYY-MM-DD') }<SyncOutlined onClick={() => reset()} style={{marginLeft: 10, color: '#0F95DA', cursor: 'pointer'}}/></p>
                <div className="daily-top">
                    <ul>
                        <li>
                            <p>日注册量</p>
                            <p><span className="daily-num">{ registrant }</span><span className="daily-font">人</span> <span className="daily-num">{ enterpriseAcount }</span><span className="daily-font">机构</span></p>
                        </li>
                        <li>
                            <p>日活跃量</p>
                            <p><span className="daily-num">{ amount }</span> <span className="daily-font">人</span>  { compare(amountCount.current.front, amountCount.current.rear) ? <i className="iconfont icon-jiantou" style={{ transform: 'rotate(180deg)', display: 'inline-block', color: '#D71B02' }}></i> : <i className="iconfont icon-jiantou" style={{color: '#0D932D'}}></i> }<span className="daily-num">{ amountCompare }</span> <span className="daily-font">人</span></p>
                        </li>
                        <li>
                            <p>日会议量</p>
                            <p><span className="daily-num">{ meeting }</span><span className="daily-font">场</span>  { compare(meetingCount.current.front, meetingCount.current.rear) ? <i className="iconfont icon-jiantou" style={{ transform: 'rotate(180deg)', display: 'inline-block', color: '#D71B02' }}></i> : <i className="iconfont icon-jiantou" style={{color: '#0D932D'}}></i> }<span className="daily-num">{ meetingCompare }</span> <span className="daily-font">人</span></p>
                        </li>
                        <li>
                            <p>日销售量</p>
                            {/* <p>三方基础版  3</p> */}
                        </li>
                    </ul>
                </div>
            </div>
            <div className="daily-content">
                <RangePicker value={[moment(start.current), moment(end.current)]} allowClear={false} onChange={(date, dateString) => {
                    start.current = new Date(dateString[0]).getTime()
                    end.current = new Date(dateString[1]).getTime()
                    init()
                }} />
                <div ref={charts1} style={{ height: 'calc(100% - 32px)', width: '100%' }}></div>
                {/* <ReactEcharts option={option} style={{ height: 'calc(100% - 32px)', width: '100%' }} /> */}
            </div>
        </>
    )
}

export default DataDaily
