import React, { useState, useEffect, useRef, useImperativeHandle, forwardRef } from 'react'
import { requestList, requestColumns, requestCustom } from '../../api/list'
import Table from 'antd/lib/table'
import Pagination from 'antd/lib/pagination'
import DatePicker from 'antd/lib/date-picker'
import Search from 'antd/lib/input/Search'
import './css/index.css'
import Tooltip from 'antd/lib/tooltip'
import moment from 'moment'

interface PropsType {
    listUrl: string;
    columnsUrl: string;
    children?: any;
    ref?: any;
    action?: any;
}

const List = forwardRef((props: PropsType, ref: any) => {
    useEffect(() => {
        getAllList()
        getColumns()
    }, [])

    useImperativeHandle(ref, ()=>({
        getAllList,
        searchArr,
        setLoading,
    }))

    const [dataSource, setDataSource] = useState<Array<any>>([]);
    const [columns, setColumns] = useState<Array<any>>([]);

    const [loading, setLoading] = useState<boolean>(false);
    const page = useRef<any>({ pageNumber: 1, pageSize: 10 })
    const total = useRef<number>(0)
    const searchArr = useRef<any[]>([])
    const [checkbox, setCheckbox] = useState<boolean>(false);

    //获取列
    const getColumns = async (): Promise<any> => {
        let response: any = await requestColumns(props.columnsUrl)
        let arr: any[] = response.data.columns.filter((item: any) => !item.checkbox)
        let columnsArr: any[] = []
        arr.forEach(async (item: any) => {
            if(!item.hide) {
                if(item.type === 'date') {
                    columnsArr.push({
                        title: item.title,
                        key: item.field,
                        search: item.search,
                        ellipsis: {
                            showTitle: false,
                        },
                        render: (text: any) => (
                            <Tooltip placement="topLeft" title={ moment(text[item.field]).format(item.format) }>
                                { moment(text[item.field]).format(item.format) }
                            </Tooltip>
                        ),
                    })
                } else if(item.type === 'select') {
                    let res: any = await requestCustom(item.url)
                    columnsArr.push({
                        title: item.title,
                        key: item.field,
                        search: item.search,
                        ellipsis: {
                            showTitle: false,
                        },
                        render: (text: any) => (
                            <Tooltip placement="topLeft" title={ res.data.find((v: any) => v.id === text[item.field]).name }>
                                { res.data.find((v: any) => v.id === text[item.field]).name }
                            </Tooltip>
                        ),
                    })
                } else {
                    columnsArr.push({
                        title: item.title,
                        dataIndex: item.field,
                        key: item.field,
                        search: item.search,
                        ellipsis: {
                            showTitle: false,
                        },
                        render: (text: any) => (
                            <Tooltip placement="topLeft" title={text}>
                                {text}
                            </Tooltip>
                        ),
                    })
                }
            }
        })
        
        setTimeout(() => {
            let checkBox: any = response.data.columns.find((item: any) => item.checkbox)
            if(checkBox) {
                setCheckbox(true)
            }
            if(props.action) {
                columnsArr.push(props.action)
            }
            setColumns(columnsArr)   
        }, 800)
    }

    //获取列表数据
    const getAllList = async (value?: string): Promise<any> => {
        let columnsArr: any[] = columns.filter((item: any) => item.search)
        if(value && value !== '') {
            searchArr.current.push({
                name: columnsArr[0].dataIndex,
                value: value,
                rule: 1
            })
        }
        setLoading(true)
        let params: any = { search: encodeURIComponent(JSON.stringify(searchArr.current)) }
        if(searchArr.current.length === 0) {
            delete params.search
        }
        setLoading(true)
        let response: any = await requestList(props.listUrl, { ...page.current, ...params })
        response.data.rows.forEach((item: any) => {
            item.key = item.primaryKey
        })
        setDataSource(response.data.rows)
        total.current = response.data.total
        searchArr.current = []
        setLoading(false)
    }

    //搜索
    const onSearch = (value: string): void => {
        getAllList(value)
    }
    
    //日期change
    const dateChange = (date: any, dateString: any): void => {
        console.log(date, dateString)
    }

    //分页change
    const pageChange = (current: number): void => {
        page.current.pageNumber = current
        getAllList()
    }

    //分页size change
    const pageSizeChange = (current: number, pageNumber: number): void => {
        page.current.pageNumber = current
        page.current.pageSize = pageNumber
        getAllList()
    }

    // rowSelection object indicates the need for row selection
    const rowSelection = {
        onChange: (selectedRowKeys: any, selectedRows: any) => {
            console.log('keys', selectedRowKeys)
            console.log('selectedRows', selectedRows)
        },
    };

    return (
        <div>
            <div className="list-top">
                <div>
                    { props.children }
                </div>
                <div className="search-box">
                    <DatePicker onChange={(date, dateString) => dateChange(date, dateString)} placeholder="全部时期" />
                    <Search onSearch={(value: any) => onSearch(value)} style={{width: '300px', marginLeft: 10}} placeholder="输入查询内容" enterButton />
                </div>
            </div>
            {
                checkbox ? (
                    <Table 
                        scroll={{ x: 'max-content' }}
                        rowSelection={{ type: 'checkbox', ...rowSelection }}
                        loading={loading} 
                        dataSource={dataSource} 
                        columns={columns} 
                        pagination={false} />
                ) : (
                    <Table 
                        scroll={{ x: 'max-content' }}
                        loading={loading} 
                        dataSource={dataSource} 
                        columns={columns} 
                        pagination={false} />
                )
            }
            <Pagination 
                current={page.current.pageNumber} 
                pageSize={page.current.pageSize} 
                showQuickJumper
                total={total.current} 
                onChange={(page) => pageChange(page)} 
                onShowSizeChange={(page, pageNumber) => pageSizeChange(page, pageNumber)}
                style={{float: 'right', paddingRight: '10px', height: 32, marginTop: 10}} />
        </div>
    )
})

export default List
