import React, { useEffect, useState } from 'react'
import { Menu, Dropdown } from 'antd';
import { AppstoreOutlined } from '@ant-design/icons';
import Views from './views'
import './css/index.css'
import { time } from 'console';
const { SubMenu } = Menu;

const Management = (props: any) => {
    const menu = (
        <Menu>
            <Menu.Item>
                <span onClick={() => {props.history.push('/')}}>返回首页</span>
            </Menu.Item>
        </Menu>
    );
    
    useEffect(() => {
        let users: any = localStorage.getItem('_user')
        if(users) {
            setUser(JSON.parse(users))
            // let item: any = JSON.parse(users).companyInfos.find((v: any) => JSON.parse(users).companyId === v.companyId)
            // setEnterpriseName(item.companyName)
        } else {
            localStorage.clear()
            window.location.hash = '#/login'
        }
    }, [])


    const [user, setUser] = useState<any>(null);
    const [enterpriseName, setEnterpriseName] = useState<string>('');

    const [key] = useState<string>(() => {
        let str = ''
        if(props.match.params.path === 'dataDaily') {
            str = 'sub2'
        } else if(props.match.params.path === 'accountStatistics') {
            str = 'sub5'
        } else if(props.match.params.path === 'meetingUser') {
            str = 'sub3'
        }
        return str
    });

    
    const handleClick = (node: any): void => {
        props.history.push(node.key)
    }
    
    return (
        <div style={{display: 'flex', backgroundColor: '#E4E4E4'}}>
            <div className="management-left">
                <div>
                    {/* <p>{ enterpriseName }</p> */}
                    <p></p>
                    <div>
                        <div>
                            <Dropdown overlay={menu} placement="bottomCenter">
                                <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                    <img src={require("../../static/images/default-photo.jpeg")} alt=""/>
                                </a>
                            </Dropdown>
                            { user && <p>{ user.name }</p> }
                        </div>
                    </div>
                    <p>企业管理员</p>
                </div>
                <div>
                    <Menu
                        onClick={handleClick}
                        style={{ width: '100%' }}
                        defaultSelectedKeys={[`/management/${props.match.params.path}`]}
                        // defaultSelectedKeys={[`/management/meetingManagement`]}
                        defaultOpenKeys={[key]}
                        mode="inline"
                        theme="dark"
                    >
                        <SubMenu key="sub2" icon={<AppstoreOutlined />} title="工作台">
                            <Menu.Item key="/management/dataDaily">数据日报</Menu.Item>
                            <Menu.Item key="/management/statisticalKanban">统计看板</Menu.Item>
                        </SubMenu>
                        <SubMenu key="sub3" icon={<AppstoreOutlined />} title="用户管理">
                            <Menu.Item key="/management/meetingUser">会议用户</Menu.Item>
                            <Menu.Item key="/management/registration">注册审核</Menu.Item>
                        </SubMenu>
                        <SubMenu key="sub4" icon={<AppstoreOutlined />} title="服务管理">
                            <Menu.Item key="/management/meetingManagement">会议管理</Menu.Item>
                        </SubMenu>
                        <SubMenu key="sub5" icon={<AppstoreOutlined />} title="客户管理">
                            <Menu.Item key="/management/accountStatistics">账户统计</Menu.Item>
                        </SubMenu>
                    </Menu>
                </div>
            </div>
            <div className="management-right">
                <div>
                    <span></span>
                    <div>
                        <p>随闻面对面</p>
                        <img src={require("../../static/images/logo.png")} />
                    </div>
                </div>
                <div>
                    <Views></Views>
                </div>
            </div>
        </div>
    )
}

export default Management
