import React from 'react'
import { Route } from 'react-router-dom'
import DataDaily from './secondPages/dataDaily'
import StatisticalKanban from './secondPages/statisticalKanban'
import MeetingUser from './secondPages/meetingUser'
import Registration from './secondPages/registration'
import MeetingManagement from './secondPages/meetingManagement'
import AccountStatistics from './secondPages/accountStatistics'

const Views = () => {

    return(
        <div>
            <Route path="/management/dataDaily" component={DataDaily}></Route>
            <Route path="/management/statisticalKanban" component={StatisticalKanban}></Route>
            <Route path="/management/meetingUser" component={MeetingUser}></Route>
            <Route path="/management/registration" component={Registration}></Route>
            <Route path="/management/meetingManagement" component={MeetingManagement}></Route>
            <Route path="/management/accountStatistics" component={AccountStatistics}></Route>
            {/* <Redirect to="/management/historicMeeting"></Redirect> */}
        </div>
    )
}

export default Views