import React, { useState, useRef } from 'react'
import List from '../../../../components/list'
import './css/index.css'
import Modal from 'antd/lib/modal'
import { message, Popconfirm } from 'antd'
import { requestGenerate } from '../../../../api/list'
import { DeleteTwoTone } from '@ant-design/icons/lib/icons'

const MeetingUser = () => {
    //表格操作列
    const [action] = useState<any>({
        title: '操作',
        key: 'action',
        width: 60,
        fixed: 'right',
        render: (text: any) => (
            <Popconfirm
                title="您确定要注销吗？"
                onConfirm={() => {
                    console.log('text', text)
                }}
                okText="确定"
                cancelText="取消"
            >
                <DeleteTwoTone style={{fontSize: 18}} />
            </Popconfirm>
        ),
    });
    const [statusScreen] = useState<Array<any>>([
        { name: '正常', status: 0 },
        { name: '待审批', status: 1 },
        { name: '注销', status: 2 },
        { name: '拒绝', status: 3 },
        { name: '所有', status: 4 },
    ]);
    const active = useRef<number>(4);
    const list = useRef<any>()
    const [random, setRandom] = useState<string>('');
    const [generateShow, setGenerateShow] = useState<boolean>(false);
    const [generateCode, setGenerateCode] = useState<string>('');

    //状态筛选
    const onScreen = (item: any): void => {
        active.current = item.status
        if(active.current !== 4) {
            list.current.searchArr.current.push({
                name: 'companyConsent',
                value: active.current,
                rule: 1
            })
        }
        list.current.getAllList()
        setRandom(Math.random().toString())
    }

    //生成邀请码
    const generate = async (): Promise<any> => {
        setGenerateShow(true)
        let response: any = await requestGenerate()
        if(response.code === 200) {
            setGenerateCode(response.data)
        } else {
            message.error(response.msg)
        }
    }

    return (
        <div className="meeting-user" style={{ overflowY: 'auto' }}>
            <List ref={list} action={action} listUrl="/admin/system/getAll" columnsUrl="/admin/system/getPageAttr">
            </List>
            <Modal
                title="邀请码"
                visible={generateShow}
                onOk={() => {
                    let doc: any = document
                    let tag: any = doc.createElement('input');
                    tag.setAttribute('id', 'cp_hgz_input');
                    tag.value = generateCode;
                    doc.getElementsByTagName('body')[0].appendChild(tag);
                    doc.getElementById('cp_hgz_input').select();
                    doc.execCommand('copy');
                    doc.getElementById('cp_hgz_input').remove();
                    setGenerateShow(false)
                }}
                onCancel={() => {setGenerateShow(false)}}
                cancelText="取消"
                okText="复制"
            >
                <p>{ generateCode }</p>
            </Modal>
        </div>
    )
}

export default MeetingUser
