import request from "./request";

/**
 * @name 列表数据获取
 */
export const requestList = (url: string, params?: any) => {
  return request('get', '/api' + url, params)
}

/**
 * @name 获取列
 */
export const requestColumns = (url: string, params?: any) => {
  return request('get', '/api' + url, params)
}

/**
 *@name 生成邀请码
 */
export const requestGenerate = () => {
    return request('get', '/api/admin/user/info/generate/invitationCode')
}

/**
 *@name 注册审核-通过
 */
export const byRegistering = (params: any) => {
    return request('get', '/api/admin/user/review/agree', params)
}
  
/**
 *@name 注册审核-拒绝
*/
export const refusing = (params: any) => {
    return request('get', '/api/admin/user/review/refuse', params)
}

/**
 *@name 会议管理-新增-修改
*/
export const refusingAddMeeting = (params: any) => {
    return request('post', '/api/admin/company/edit', params)
}

/**
 *@name 会议管理-删除
*/
export const refusingDelMeeting = (params: any) => {
    return request('get', '/api/admin/company/delete', params)
}

/**
 *@name 自定义
*/
export const requestCustom = (url: string, params?: any) => {
    return request('get', '/api' + url, params)
}
