import React, { useState, useRef } from 'react'
import { refusingAddMeeting, refusingDelMeeting } from '../../../../api/list'
import List from '../../../../components/list'
import './css/index.css'
import Button from 'antd/lib/button'
import Modal from 'antd/lib/modal'
import { Form, Input, message, Popconfirm } from 'antd'

const AccountStatistics = () => {
    const [action] = useState<any>({
        title: '操作',
        key: 'action',
        width: 200,
        fixed: 'right',
        render: (text: any) => (
            <div>
                <Button onClick={() => {
                    compileForm.setFieldsValue(text)
                    setCompileVisible(true)
                }} type="primary" style={{marginRight: 10}}>编辑</Button>
                <Popconfirm
                    title="您确定要删除吗？"
                    onConfirm={async (): Promise<any> => {
                        list.current.setLoading(true)
                        let response: any = await refusingDelMeeting({ primaryKey: text.primaryKey })
                        if(response.code === 200) {
                            list.current.getAllList()
                            message.success('删除成功')
                        } else {
                            message.error(response.msg)
                        }
                    }}
                    okText="确定"
                    cancelText="取消"
                >
                    <Button type="primary">删除</Button>
                </Popconfirm>
            </div>
        ),
    });
    const [addVisible, setAddVisible] = useState<boolean>(false);
    const [compileVisible, setCompileVisible] = useState<boolean>(false);
    const [submitLoding, setSubmitLoding] = useState<boolean>(false);
    const [form] = Form.useForm();
    const [compileForm] = Form.useForm();
    const list = useRef<any>()

    return (
        <div className="account-statistics">
            <List ref={list} action={action} listUrl="/admin/company/getAll" columnsUrl="/admin/company/getPageAttr">
                <Button onClick={() => setAddVisible(true)} type="primary">新增</Button>
            </List>
            <Modal
                visible={addVisible}
                title="新增"
                cancelText="取消"
                okText="确定"
                confirmLoading={submitLoding}
                onCancel={() => setAddVisible(false)}
                onOk={() => {
                    form.validateFields().then(async (params: any): Promise<any> => {
                        setSubmitLoding(true)
                        let response: any = await refusingAddMeeting(params)
                        if(response.code === 200) {
                            message.success('新增成功！')
                            form.resetFields();
                            setSubmitLoding(false)
                            setAddVisible(false)
                            list.current.getAllList()
                        } else {
                            message.error(response.msg)
                        }
                    })
                }}
            >
                <Form
                    form={form}
                    layout="vertical"
                    name="form_in_modal"
                >
                    <Form.Item name="name" label="企业名"
                        rules={[{ required: true, message: '企业名不能小于3位', min: 4 }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item name="unifiedCreditCode" label="统一社会信用代码" 
                        rules={[{ required: true, message: '请输入统一社会信用代码' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item name="organizationCode" label="组织机构代码"
                        rules={[{ required: true, message: '请输入组织机构代码' }]}
                    >
                        <Input />
                    </Form.Item>
                </Form>
            </Modal>
            <Modal
                visible={compileVisible}
                title="新增"
                cancelText="取消"
                okText="确定"
                confirmLoading={submitLoding}
                onCancel={() => setCompileVisible(false)}
                onOk={() => {
                    compileForm.validateFields().then(async (params: any): Promise<any> => {
                        setSubmitLoding(true)
                        let response: any = await refusingAddMeeting(params)
                        if(response.code === 200) {
                            message.success('编辑成功！')
                            compileForm.resetFields();
                            setSubmitLoding(false)
                            setCompileVisible(false)
                            list.current.getAllList()
                        } else {
                            message.error(response.msg)
                        }
                    })
                }}
            >
                <Form
                    form={compileForm}
                    layout="vertical"
                    name="form_in_modal"
                >
                    <Form.Item name="name" label="企业名"
                        rules={[{ required: true, message: '企业名不能小于3位', min: 4 }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item name="unifiedCreditCode" label="统一社会信用代码" 
                        rules={[{ required: true, message: '请输入统一社会信用代码' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item name="organizationCode" label="组织机构代码"
                        rules={[{ required: true, message: '请输入组织机构代码' }]}
                    >
                        <Input />
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    )
}

export default AccountStatistics
